.NavBar-container{
    height: 10vh;
    width: 100%;
    top: 0;
    position: fixed;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
    z-index: 100;
    background-color: #fff;
    border-bottom: var(--colorBase) solid 5px;
}
.NavBar{
    height: inherit;
    width: inherit;
    position: relative;
    
}
.NavBar-container img{
    height: 100%;
}


